import React from 'react'
import { Link } from 'react-router-dom'
import { Routes } from '../../App'

export interface ArticleTeaserProps {
    id?: string
    titleImage: string
    title: string
    buttonTitle?: string
    route?: Routes
}

const ArticleTeaser: React.FC<ArticleTeaserProps> = (props) => {

    let button = props.buttonTitle && props.route ? (
        <ul className="actions">
                <li>
                    <Link className="button big" to={props.route}>{props.buttonTitle}</Link>
                </li>
        </ul>
    ) : null

    return (
        <article id={props.id}>
            <a href="#" className="image"><img src={props.titleImage} alt="" /></a>
            <h3>{props.title}</h3>
            <p>{props.children}</p>
            {button}
        </article>
    )
}

export default ArticleTeaser